function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        spaceBetween: 0,
        slidesPerView: "auto",
        freeMode: true,
        watchSlidesProgress: true,
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 0,
        slidesPerView: "auto",
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: '.gallery .swiper-button-next',
            prevEl: '.gallery .swiper-button-prev',
        },
    });
}

function Sliders() {
    let sliders = document.querySelectorAll("._slider")
    let SlidersArray = []

    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        SlidersArray[index] = new Swiper(`._slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            navigation: {
                nextEl: `._slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `._slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `._slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'progressbar',
            },
        })
    })
}


let tabBlocks = document.querySelectorAll("._tabs-parent")

tabBlocks.forEach(tabBlock => {
    let tabs = tabBlock.querySelectorAll("._tab")
    let tabContents = tabBlock.querySelectorAll("._tab-content")

    tabs.forEach(tab => {
        tab.onclick = function(){
            let tabId = this.dataset.tab

            tabs.forEach(tab => {
                tab.classList.remove("_active")
            })
            tab.classList.add("_active")

            tabContents.forEach(tabContent => {
                tabContent.classList.remove("_active")
            })
            tabBlock.querySelector(`._tab-content.${tabId}`).classList.add("_active")
        }

    })
})

gallery()
Sliders()
